import React from 'react';
import Gif from './Gif';
import ls from 'local-storage';

class Gifs extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      start: this.getAllGifs(),
      Gifs: '',
      isLoaded: false
    }
  }
  getAllGifs = () => {
    const userId = ls.get('userId');
    const url = `https://teamworksng.herokuapp.com/api/v1/gifs/all/${userId}`;
    fetch(url, {
      method: 'GET',
      mode: 'cors',
      headers: {
        "Content-Type": "application/json",
        "Authorization": "Bearer " + ls.get('token')
      }
    })
    .then((res) => res.json())
    .then((result) => {
      this.setState((prevState) => {
        prevState.Gifs = result.data;
        prevState.isLoaded = true;
        return {
          Gifs: prevState.Gifs,
          isLoaded: prevState.isLoaded
        }
      });
    })
    .catch(e => console.error(e))
  }
  render() {
    let gifs = [];
      if(this.state.isLoaded) {
        const gifPage = this.state.Gifs.filter( (gif, count) => count <= 10);
        gifPage.forEach(gif => gifs.push(<Gif gif={gif} key={gif.id} />));
      }
    return (
      <div className='gifs'>
        {gifs}
        <div>
          <input type='button' value='NEXT' onClick={this.nextPage}/>
        </div>
      </div>
    );
  }
}

export default Gifs;
